import {NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {RouterModule, Routes} from '@angular/router';
import { FormsModule } from '@angular/forms';
import { Ng2SearchPipeModule } from 'ng2-search-filter';

import { RecipeService } from './services/recipe.service';

import { AppComponent } from './app.component';
import { RecipeComponent } from './recipe/recipe.component';
import { ListRecipeComponent } from './list-recipe/list-recipe.component';
import { MenuComponent } from './menu/menu.component';
import { MainComponent } from './main/main.component';
import { AllRecipeComponent } from './all-recipe/all-recipe.component';
import { OneRecipeComponent } from './one-recipe/one-recipe.component';
import { FourOhFourComponent } from './four-oh-four/four-oh-four.component';
import {SearchRecipeComponent} from './search-recipe/search-recipe.component'

const appRoutes: Routes = [
  { path: 'cookingLucie', component: MainComponent},
  { path:'connexion', component: MainComponent},
  { path: 'recettes', component: SearchRecipeComponent},
  { path:':page', component: OneRecipeComponent},
  { path: '', component: MainComponent},
  { path: 'not-found', component: FourOhFourComponent},
  { path: '**', redirectTo: 'not-found'}
];

@NgModule({
  declarations: [
    AppComponent,
    RecipeComponent,
    ListRecipeComponent,
    MenuComponent,
    MainComponent,
    AllRecipeComponent,
    OneRecipeComponent,
    FourOhFourComponent,
    SearchRecipeComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    RouterModule.forRoot(appRoutes),
    Ng2SearchPipeModule,
  ],
  providers: [RecipeService],
  bootstrap: [AppComponent],
})
export class AppModule { }

